.full-width__img {
	width: 100%;
}

.main-big {
	margin-top: 50px;
}

.xs__img {
	height: 280px;

	@include breakpoint(max-width 790px) {
		height: 360px;
	}
}

.l__img {
	height: 670px;
}

.xl__img {
	height: 600px;
}

.xs__img,
.sm__img,
.m__img,
.l__img,
.xl__img {
	width: 100%;
	object-fit: cover;

	@include breakpoint(max-width 1140px) {
		height: auto;
	}
}



.main__nav {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	font-size: 19px;

	a {
		margin-top: 20px;
		line-height: 1.68;
		opacity: 0.5;

		&:first-child {
			margin-top: 0;
		}

		&:hover {
			@include textShadow(white);
			@include smallUnderlineCase(white, black);
			opacity: 1;
		}

		@include breakpoint(max-width 1140px) {
			margin-top: 0;
			margin-right: 30px;

			&:last-child {
				margin-right: 0;
			}
		}
	}

	@include breakpoint(max-width 1140px) {
		flex-direction: row;
		white-space: nowrap;
		overflow-x: auto;
		padding-bottom: 5px;
	}
}

figure > div {
	a {
		display: block;
		margin-top: 50px;

		&:first-child {
			margin-top: 0;
		}

		@include breakpoint(max-width 1140px) {
			width: calc(50% - 25px);
			margin-top: 0;
			margin-right: 50px;

			&:last-child {
				margin-right: 0;
			}
		}

		@include breakpoint(max-width 790px) {
			width: 100%;
			margin-top: 30px;
			margin-right: 0;
		}
	}

	@include breakpoint(max-width 1140px) {
		display: flex;
		flex-wrap: wrap;
		overflow-x: auto;
		overflow-y: hidden;
	}

}

